$second-section-color: #FFF5F6;
$primary-header-color: #FFA5BA;
$accent-color: #D41443;
$primary-text-color: #907E82;

.container {
    outline: 1px solid black;
}

.footer {

    position: relative;

    background-color: $second-section-color;
    min-width: 320px;

    &__text-box {
        outline: 1px solid orange;
    
        margin-bottom: 43px;
    }

    &__title{
        outline: 2px solid blue;

        text-align: left;
        font-family: $font-secondary;
        font-weight: 400;
        display: block;
        text-transform: uppercase;

        &--one {
            font-size: 18px;
            line-height: 1.167;
           
            color: $primary-header-color;
            margin-bottom: 17px;
            width: 232px;
        }
    
        &--two {
            font-size: 34px;
            line-height: 1.147;
            letter-spacing: 0.04em;
            color: $accent-color;
            width: 200px;
        }
    }
}

.social-box {
    outline: 1px solid cyan;
   
    margin-bottom: 46px;
    
    &__list {
       outline: 1px solid green;
     

       &:not(:first-child) {
           margin-left: 28px;
       }
    }
    &__links {
        display: flex;
        justify-content: flex-end;
    }
    &__icon {
       display: block;
       fill: $primary-header-color;

       &:hover,
       &:focus {
           fill: $accent-color;
       }
    }
}

.contact-box {
    outline: 1px solid orange;

    font-weight: 700;
    font-size: 12px;
    line-height: 1.86;
    text-align: right;
    
    &__phone {
        color: $accent-color;
    }
}

.copyright-box {
    outline: 1px solid red;
    margin-top: 43px;
    display: flex;
    align-items: center;
    justify-content: center;

    border-top: 1px solid $border-footer-color;
    padding-top: 22px;
    padding-bottom: 25px;
    &__text {
        
        color: $primary-text-color;
        font-weight: 700;
        font-size: 14px;
        line-height: 1.52;
        text-align: center;

        max-width: 150px;
        @media screen and (min-width: $tablet){
            max-width: 276px;
            letter-spacing: 0.02em;
            padding-top: 29px;
            padding-bottom: 18px;
        }
        outline: 1px solid blue;
    }
  
}